<template>
  <div class="container mt-5">
    <h2>Admin Dashboard</h2>
    <p>Welcome, admin!</p>
    <button @click="logout" class="btn btn-danger mt-3">Logout</button>
  </div>
</template>

<script>
export default {
  name: "AdminDashboard",
  methods: {
    async logout() {
      await fetch("/logout", {
        method: "POST",
        credentials: "include",
      });
      this.$router.push("/");
    },
  },
};
</script>
